<template>
  <div id="main-container">
    <div class="notice-title">
          * 구매날짜와 가격이 입력 된 정보만 조회됩니다.
    </div>
    <div class="notice-title">
      * 위시리스트는 제외됩니다.
    </div>
    <table
      v-for="(year, yearIndex) in yearlyFigureOut" :key="yearIndex"
      class="fixed">
      <caption>
        {{ year.year }} {{ $route.meta.title }}
      </caption>
      <thead>
      <tr>
        <th scope="col">월</th>
        <th scope="col">갯수</th>
        <th scope="col">합계</th>
      </tr>
      </thead>
      <tbody>
      <tr
        v-for="(month, monthIndex) in year.monthlyFigureOut" :key="monthIndex"
        class="hover-tr"
      >
        <th scope="col">{{ month.month }}월</th>
        <td data-label="No">{{ month.count }}</td>
        <td
          v-if="month.total !== 0"
          data-label="No">{{ String(month.total).replace(/^0+|\D+/g, '').replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,') }}
        </td>
        <td v-else>
          0
        </td>
      </tr>
      <tr class="hover-tr">
        <th>총계</th>
        <td>{{ year.totalCount }}</td>
        <td v-if="year.totalPrice !== 0">
          {{ String(year.totalPrice).replace(/^0+|\D+/g, '').replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,') }}
        </td>
        <td v-else>
          0
        </td>
      </tr>
      </tbody>
    </table>
    <div class="input-wrapper" style="margin-top: 10px;">
      <button
        @click="_routeGoBack"
        class="cancel__button">
        뒤로가기
      </button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'pinia'
import { usersStore } from '@/store/users'
import { figureStore } from '@/store/figures'
import sweetAlert from '@/wrapper/sweet-alert'
import jwtDecode from 'jwt-decode'

export default {
  name: 'user-gunpla-monthly-purchase',
  data () {
    return {
      currentYear: new Date().getFullYear(),
      yearlyFigureOut: [
        {
          year: 0,
          monthlyFigureOut: [
            { month: 0, count: 0, total: 0 }
          ],
          totalCount: 0,
          totalPrice: 0
        }
      ]
    }
  },
  computed: {
    ...mapState(usersStore, ['userCustomInfo', 'loggedInAccessToken'])
  },
  methods: {
    ...mapActions(figureStore, ['setFigureMonthlyParam']),
    _routeGoBack () {
      this.$router.go(-1)
    },
    _getMonthlyFigureTotalInfo (year) {
      this.getMonthlyFigureTotalInfo(this.userCustomInfo.userOid, year).then(res => {
        return res
      })
    }
    // async _routeToMonthlyDetail (year, month) {
    //   const figureMonthlyParam = {
    //     year: year,
    //     month: month
    //   }
    //   await this.setFigureMonthlyParam(figureMonthlyParam)
    //   await this.$router.push({ name: 'user-gunpla-monthly-detail' })
    // }
  },
  created () {
    if (this.userCustomInfo === undefined) {
      sweetAlert.noIcon('잘못된 경로로 진입하셨거나 인증 정보가 없습니다. 로그인 화면으로 이동합니다.', '확인')
      this.logout()
    } else {
      if (new Date(jwtDecode(this.loggedInAccessToken).exp * 1000) <= new Date()) {
        sweetAlert.noIcon('자동 로그인 기간이 만료 되었습니다. 다시 로그인 해 주세요 :)', '확인')
        this.logout()
      }
    }
  },
  mounted () {
    this.getMonthlyFigureInfo(this.userCustomInfo.userOid).then(res => {
      this.yearlyFigureOut = res
    })
  }
}
</script>
<style scoped lang="scss">
.grid-item {
  height: 160px !important;
}
.button__wrapper {
  margin-top: 30px;
  text-align: right;
}
</style>
